@font-face {
  font-family: 'Rubik';
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url('./rubik-v21-latin_cyrillic-regular.eot'), /* IE9 Compat Modes */
       url('./rubik-v21-latin_cyrillic-regular.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
       url('./rubik-v21-latin_cyrillic-regular.woff2') format('woff2'), /* Super Modern Browsers */
       url('./rubik-v21-latin_cyrillic-regular.woff') format('woff'), /* Modern Browsers */
       url('./rubik-v21-latin_cyrillic-regular.ttf') format('truetype'), /* Safari, Android, iOS */
       url('./rubik-v21-latin_cyrillic-regular.svg#Rubik') format('svg'); /* Legacy iOS */
}

